function confirm(title, content, okCall) {
  swal({
    title,
    text: content,
    icon: 'warning',
    buttons: ['取消', '确定'],
    dangerMode: true
  })
    .then((willDelete) => {
      if (willDelete) {
        okCall()
      }
    })
}

function confirminput(title, code, content, okCall) {
  projectApi.companygetidentity({ code: code }).then(resp => {
    if (resp.status === 200) {
      swal({
        title: title,
        text: '请输入短信验证码',
        content: 'input',
        showCancelButton: true,
        closeOnConfirm: false,
        animation: 'slide-from-top',
        inputPlaceholder: '请输入短信验证码...',
        buttons: ['取消', '确定']
      }).then((willDelete) => {
        if (willDelete === false) {
          return false
        }
        if (willDelete === '') {
          error('请输入短信验证码')
          return false
        }
        if (willDelete) {
          okCall(willDelete)
        }
      })
    }
  }, error => {
    // error('获取短信验证码失败', findErrorMsg(error))
    notify.error({
      title: '获取短信验证码失败',
      message: findErrorMsg(error)
    })
  })
}

