function format(date, formatter) {
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hours = date.getHours()
  const min = date.getMinutes()
  const se = date.getSeconds()
  return formatter.replace('YYYY', year)
    .replace('MM', month < 10 ? '0' + month : month)
    .replace('dd', day < 10 ? '0' + day : day)
    .replace('HH', hours < 10 ? '0' + hours : hours)
    .replace('mm', min < 10 ? '0' + min : min)
    .replace('ss', se < 10 ? '0' + se : se)
}

function formatCh(number) {
  const date = new Date(number * 1000)
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hours = date.getHours()
  const min = date.getMinutes()
  const se = date.getSeconds()
  return `${date.getFullYear()}年${month < 10 ? '0' + month : month}月${day < 10 ? '0' + day : day}日${hours < 10 ? '0' + hours : hours}时${min < 10 ? '0' + min : min}分${se < 10 ? '0' + se : se}`
}

export default { format, formatCh }
