import axios from 'axios'
import qs from 'qs'
import { getItem } from '@/utils/funcs'

function extendData(dist, ...sources) {
  const result = dist
  sources.forEach((s) => {
    if (s) { // 排除无自定义data
      Object.keys(s).forEach((key) => {
        if (Object.hasOwnProperty.call(s, key)) {
          result[key] = s[key]
        }
      })
    }
  })
  return result
}

function get(url, params, config) {
  return axios.get(url, extendData({}, {
    params: extendData({}, params, { _timeStamp: Date.now() }),
    // params: extendData({}, params),
    headers: { 'X-API-TOKEN': getItem('Token') }
  }, config))
    .then((success) => {
      return Promise.resolve(success.data)
    }, (e) => {
      if (e.response && e.response.status === 401 && window.location.hash.indexOf('/login') < 0) {
        window.appGlobal.toLogin()
      }
      return Promise.reject(e)
    })
}

function post(url, data, config) {
  return axios.post(url, data, config)
    .then((success) => {
      return Promise.resolve(success.data)
    }, (e) => {
      if (e.response && e.response.status === 401 && window.location.hash.indexOf('/login') < 0) {
        window.appGlobal.toLogin()
      }
      return Promise.reject(e)
    })
}

function postWithJson(url, data, config) {
  return axios.post(url, data, extendData({}, {
    headers: {
      // 'Content-Type': 'application/json;charset=UTF-8'
      // 'X-API-TOKEN': getItem('Token')
    }
  }, config))
    .then((success) => {
      return Promise.resolve(success.data)
    }, (e) => {
      if (e.response && e.response.status === 401 && window.location.hash.indexOf('/login') < 0) {
        window.appGlobal.toLogin()
      }
      return Promise.reject(e)
    })
}

function put(url, data, config) {
  return axios.put(url, qs.stringify(extendData({}, data)), {
    headers: {
      // 'Content-Type': 'application/json;charset=UTF-8',
      // 'X-API-TOKEN': getItem('Token')
    }
  }, config)
}

function putWithJson(url, data, config) {
  return axios.put(url, data, extendData({}, {
    headers: {
      // 'Content-Type': 'application/json;charset=UTF-8',
      // 'X-API-TOKEN': getItem('Token')
    }
  }, config))
    .then((success) => {
      return Promise.resolve(success.data)
    }, (e) => {
      if (e.response && e.response.status === 401 && window.location.hash.indexOf('/login') < 0) {
        window.appGlobal.toLogin()
      }
      return Promise.reject(e)
    })
}

function del(url, config) {
  return axios.delete(url, {
    headers: {
      // 'Content-Type': 'application/json;charset=UTF-8',
      // 'X-API-TOKEN': getItem('Token')
    }
  }, config).then((success) => {
    return Promise.resolve(success)
  }, (e) => {
    if (e.response && e.response.status === 401 && window.location.hash.indexOf('/login') < 0) {
      window.appGlobal.toLogin()
    }
    return Promise.reject(e)
  })
}

function download(url, fileName) {
  const xhr = new XMLHttpRequest()
  xhr.open('GET', url, true) // 也可以使用POST方式，根据接口
  xhr.responseType = 'blob' // 返回类型blob
  xhr.setRequestHeader('X-API-TOKEN', getItem('Token'))
  // 定义请求完成的处理函数，请求前也可以增加加载框/禁用下载按钮逻辑
  xhr.onload = function() {
    // 请求完成
    if (this.status === 200) {
      // 返回200
      const blob = this.response
      if (window.navigator && window.navigator.msSaveOrOpenBlob) { // for IE
        window.navigator.msSaveOrOpenBlob(blob, fileName)
      } else { // for Non-IE (chrome, firefox etc.)
        const a = document.createElement('a')
        document.body.appendChild(a)
        a.style = 'display: none'
        const url = URL.createObjectURL(blob)
        a.href = url
        a.download = fileName
        if (url.length > 8182) {
          alert('文件大小超出限制')
        } else {
          a.click()
          a.remove()
        }
      }
    }
  }
  // 发送ajax请求
  xhr.send()
}

export default {
  get,
  post,
  postWithJson,
  putWithJson,
  put,
  del,
  download
}
