// import axios from 'axios'
import constant from '@/common/constant'
import http from '@/utils/http'

export default class Monitor {
  static store(payload) {
    const param = {
      mid: payload.username,
      pwd: payload.password,
      identity_type: 0
    }
    return http.postWithJson(`${constant.baseUrl}/v3/member/auth`, param)
  }

  static initMonitorAlarms(params) {
    return http.get(`${constant.baseUrl}/v2/tasks`, params)
  }
}

export function handleTask(params) {
  return http.putWithJson(`${constant.baseUrl}/v2/task`, params)
}

export function addTask(params) {
  return http.postWithJson(`${constant.baseUrl}/v2/task`, params)
}
