import Place from '@/api/place'
import msgUtil from '@/utils/msgUtil'

export default {
  state: {
    checkItem: { labels: [], place: {}, supervisors: [] }, // 选择的项目
    buildings: {}
  },
  getters: {
  },
  actions: {
    async findPlaceList({ commit, dispatch }, payload) {
      try {
        const { data } = await Place.findPlaceList(payload)
        console.log(data)

        if (data.items.length > 0) {
          dispatch('checkItem', data.items[0].id)
        } else {
          commit('checkItem', { labels: [], place: {}, supervisors: [] })
        }
        return data
      } catch (error) {
        console.log(error)
      }
    },

    async checkItem({ commit, dispatch }, id) {
      try {
        const { data } = await Place.findPlaceDetail(id)
        console.log(data)
        commit('checkItem', data)
        // dispatch('findBuildings', id)
      } catch (error) {
        msgUtil.error('加载项目数据失败', msgUtil.findErrorMsg(error))
      }
    },
    async findBuildings({ commit }, placeId) {
      try {
        const { data } = await Place.placeBuildings(placeId)
        commit('refreshBuildings', { buildings: data, placeId })
      } catch (error) {
        msgUtil.error('加载项目数据失败', msgUtil.findErrorMsg(error))
      }
    }
  },
  mutations: {
    checkItem(state, item) {
      state.checkItem = item
      state.buildings = {}
    },
    refreshBuildings(state, { buildings, placeId }) {
      if (state.checkItem.place.id === placeId) {
        state.buildings = buildings
      }
    }
  }
}
