import constant from '@/common/constant'
import Http from '@/utils/http'

function findPlaceList(query) {
  return Http.get(`${constant.baseUrl}/v2/places`, query)
}

function findPlaceListall(query) {
  return Http.get(`${constant.baseUrl}/v3/places/all`, query)
}

function findPlaceDetail(id) {
  return Http.get(`${constant.baseUrl}/v3/place/${id}`)
}

function savePlace(data, kind) {
  if (kind == 'add') {
    return Http.postWithJson(`${constant.baseUrl}/v3/place`, data)
  } else {
    return Http.putWithJson(`${constant.baseUrl}/v3/place`, data)
  }
}

function deletePlace(id) {
  return Http.del(`${constant.baseUrl}/v2/place/${id}`)
}

function deletePlacenew(id, verifycode) {
  return Http.del(`${constant.baseUrl}/v3/place?pid=` + id + `&verify_code=` + verifycode)
}

function placegetidentity(data) {
  return Http.get(`${constant.baseUrl}/v3/place/smscode/identity`, data)
}

function companygetidentity(data) {
  return Http.get(`${constant.baseUrl}/v3/company/smscode/identity`, data)
}

function membergetidentity(data) {
  return Http.get(`${constant.baseUrl}/v3/member/smscode/identity`, data)
}

function Userzlist(id) {
  return Http.get(`${constant.baseUrl}/v2/supervisor/${id}`)
}

function addUser(data) {
  return Http.postWithJson(`${constant.baseUrl}/v2/supervisor`, data)
}

function addUserz(data) {
  return Http.postWithJson(`${constant.baseUrl}/v2/common/supervisor`, data)
}

function deleteUser(userId) {
  return Http.del(`${constant.baseUrl}/v2/supervisor/${userId}`)
}

function deleteUserz(userPhone, Id) {
  return Http.del(`${constant.baseUrl}/v2/common_supervisor/${Id}/${userPhone}`)
}

function updateUser(data) {
  return Http.putWithJson(`${constant.baseUrl}/v2/supervisor`, data)
}

function updateUserz(data) {
  return Http.putWithJson(`${constant.baseUrl}/v2/common/supervisor`, data)
}

function placeBuildings(placeId) {
  return Http.get(`${constant.baseUrl}/v2/place/${placeId}/buildings`)
}

export default {
  findPlaceList,
  findPlaceListall,
  findPlaceDetail,
  savePlace,
  deletePlace,
  deletePlacenew,
  placegetidentity,
  companygetidentity,
  membergetidentity,
  Userzlist,
  addUser,
  addUserz,
  deleteUser,
  deleteUserz,
  updateUser,
  updateUserz,
  placeBuildings
}
