import User from '@/api/user'

export default {
  state: {
    // token: getToken(),
    hasLogin: false
  },
  getters: {
  },
  actions: {
    async handleLogin({ commit }, payload) {
      const { data } = await User.store(payload)
      console.log(data)
      commit('SET_LOGON')
      window.localStorage.setItem('Token', data.api_token)
      return data
    },
    // 获取用户相关信息
    setUserInfo({ state, commit }, data) {
      console.log(data)
      commit('setHasGetInfo', true)
    }
  },
  mutations: {
    SET_LOGON: (state) => {
      state.hasLogin = true
    }
  }
}
