import constant from '@/common/constant'
import { getItem } from './funcs'

export default {
  roles: [],
  init(roles) {
    this.roles = roles || []
  },
  hasAuth(auth) {
    if (this.roles.length === 0) {
      const authPermissions = getItem(constant.authPermissions)
      this.init(authPermissions ? authPermissions.split(',') : '')
    }
    return this.roles.indexOf(auth) >= 0
  }
}
