import Monitor from '@/api/monitor'
import dateUtil from '@/utils/date_util'
import constant from '@/common/constant'

function convertTask(o, alarmTypeNames) {
  return {
    id: o.id,
    mac: o.mac,
    adminUser: o.publisher,
    handleUser: o.receiver,
    alarmTime: dateUtil.format(new Date(o.alarm_time * 1000), 'YYYY-MM-dd HH:mm:ss'),
    createTime: o.create_time ? dateUtil.format(new Date(o.create_time * 1000), 'YYYY-MM-dd HH:mm:ss') : '',
    endTime: o.end_time ? dateUtil.format(new Date(o.end_time * 1000), 'YYYY-MM-dd HH:mm:ss') : '',
    alarmType: constant.alarmReasons[o.alarm_type],
    alarmTypeName: (alarmTypeNames[o.device_type] && alarmTypeNames[o.device_type].replace('告警', '').replace('设备', '') + '设备') || '未知',
    address: o.address,
    phone: o.phone,
    memo: o.memo
  }
}

export default {
  state: {
    alarms: {
      untreated: [],
      pending: [],
      complete: []
    }
  },
  getters: {
  },
  actions: {
    async initMonitorAlarms({ commit }) {
      // 处理中
      const peddingParams = { page: 1, per_page: 25, status: 2 }
      const { data } = await Monitor.initMonitorAlarms(peddingParams)
      const alarmTypeNames = constant.alarmTypes
      commit('initPending', data.items.map(i => {
        return convertTask(i, alarmTypeNames)
      }))
      // 处理完毕
      const completeParams = { page: 1, per_page: 25, status: 4 }
      const { data: data2 } = await Monitor.initMonitorAlarms(completeParams)
      console.log(data2)
      commit('initComplete', data2.items.map(i => {
        return convertTask(i, alarmTypeNames)
      }))
      // // 处理中
      // http.queryTask({ page: 1, per_page: 25, status: 2 }).then(resp => {
      //   commit('initPending', resp.data.items.map(i => {
      //     return convertTask(i, alarmTypeNames)
      //   }))
      // }, error => {
      //   msgUtil.error('查询工单失败', msgUtil.findErrorMsg(error))
      // })
      // // 处理完毕
      // http.queryTask({ page: 1, per_page: 25, status: 4 }).then(resp => {
      //   commit('initComplete', resp.data.items.map(i => {
      //     return convertTask(i, alarmTypeNames)
      //   }))
      // }, error => {
      //   msgUtil.error('查询工单失败', msgUtil.findErrorMsg(error))
      // })
    }
  },
  mutations: {
    initPending(state, data) {
      state.alarms.pending = data
    },
    initComplete(state, data) {
      console.log(data)
      state.alarms.complete = data
    }
  }
}
